nav {
    position: relative;
    display: flex;
    justify-content: center;

    .nav__container {
        background-color: rgba(190, 201, 241, 0.63);
        display: flex;
        position: fixed;
        height: fit-content;
        width: fit-content;
        bottom: 1.5rem;
        padding: 0;
        backdrop-filter: blur(4px);
        border-radius: 30px;
        box-shadow: 2px 2px 20px rgb(120, 116, 116);
        justify-content: space-evenly;
        align-items: center;
        z-index: 999;

        a {
            display: flex;
            align-items: center;
            margin: .5rem .5rem;
            padding: 1rem;
            border-radius: 50%;
            font-size: 1.2rem;
        }
    }
}

@media only screen and (max-width:1024px) {
    nav {
        .nav__container {
            bottom: 1rem;
        }
    }
}

@media only screen and (max-width:600px) {
    nav {
        .nav__container {
            bottom: 0;
            width: 100%;
            border-radius: 0;
        }
    }

}