@import url('https://fonts.googleapis.com/css2?family=Alumni+Sans+Pinstripe:ital@0;1&family=Poppins:wght@300;500;800&display=swap');

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;

}

:root {
    /* Color Palette */
    --bg-color: #F2F3F4;
    --accent-color: #F2CD5D;
    --primary-color: #5FAAE3;
    --dark-color: #246BCF;
    --light-color: #7fa4d8;
    --succes-color: #71A889;
    --border-color: grey;

    /* Break points */
    --width-lg: 70%;
    --width-md: 83%;
    --width-sm: 95%;
    /* Animations */
    --default-transition: all 300 ease;

    --shadow: inset 1px 2px 20px;
}

html {
    overflow-x: hidden !important;
    scroll-behavior: smooth;
    
}

::-webkit-scrollbar {
    display: none;

}

body {
    font-family: 'Popins', sans-serif;
    line-height: 1.6;
    overflow-x: hidden !important;
    position: relative;
}

section {
    width: 100%;
    height: fit-content;
}

/* General Styles */
.container {
    width: var(--width-lg);
    height: fit-content;
    margin: 0 auto;

}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

img {
    display: inline-block;
}

a {

    color: var(--dark-color);
}

a:hover {
    color: var(--light-color);
}

/* Media queries */

@media only screen and (max-width:1024px) {
    .container {
        width: var(--width-md);
    }
}

@media only screen and (max-width: 600px) {
    .container {
        width: var(--width-sm);
    }
}